<template>
  <div>
    <el-table :data="tableData" highlight-current-row border style="width: 100%" @row-click="handRowClick">
        <el-table-column prop="repo" label="项目"></el-table-column>
        <el-table-column prop="today_stars" label="今日star数"></el-table-column>
        <el-table-column prop="stars" label="总star数"></el-table-column>
        <el-table-column prop="type" label="类型"></el-table-column>
        <el-table-column prop="desc" label="描述"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import {getGithub} from '../api'

export default {
  name: 'Github',
  data () {
    return {
        tableData: []
        // rowStyle: {

        // }
    }
  },
  methods: {
    handRowClick(row, event, column) {
        window.open(row.url)
    }
  },
  mounted: function() {
    getGithub()
      .then(response => this.tableData = response.data)
      .catch(error => console.log(error));
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



